import { useEffect, useState } from "react";
import axios from "axios";
import toastr from "toastr";
import "./styles.css";
import ClipLoader from "react-spinners/ClipLoader";

const types = [
  { name: "Robot", value: 1, emoji: "ðŸ¤–" },
  { name: "Alien", value: 2, emoji: "ðŸ‘½" },
  { name: "Robot Head", value: 3, emoji: "ðŸ¤–" },
  { name: "Cat", value: 4, emoji: "ðŸ±" }
];

export default function App() {
  const [robohash, setRobohash] = useState("");
  const [currType, setCurrType] = useState(0);
  const [imageURL, setImageURL] = useState("");
  const [isLoaded, setIsLoaded] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  const url = `https://robohash.org/${robohash}.png?set=set${currType}`;

  useEffect(() => {
    const request = axios.CancelToken.source();

    const fetchPost = async () => {
      try {
        setIsLoaded(false);
        setLoading(true);
        setError(false);
        const { status } = await axios.get(url, {
          cancelToken: request.token
        });
        setIsLoaded(status === 200);
        setError(status !== 200);
        setLoading(false);
      } catch (err) {
        console.log("There was a problem or request was cancelled.");
      }
    };
    if (robohash) {
      fetchPost();
      setImageURL(url);
    }

    return () => request.cancel();
  }, [currType]);

  function onGenerate(e) {
    e.preventDefault();
    if (currType && robohash) {
      setImageURL(url);
    } else if (!currType) {
      toastr.warning("You didn't select a type");
    } else if (!robohash) {
      toastr.warning("Enter a text");
    }
  }

  return (
    <div className="App">
      <h1>RoboHash</h1>
      <h2>GENERATE UNIQUE IMAGES FROM ANY TEXT</h2>
      <form onSubmit={onGenerate} className="form">
        <input
          type="text"
          onChange={(e) => setRobohash(e.target.value)}
          placeholder="Enter any text here... âœŽ"
        />

        <select value={currType} onChange={(e) => setCurrType(e.target.value)}>
          <option value={0} disabled>
            Select a Type &nbsp; ðŸ‘ˆ
          </option>
          {types.map((type) => {
            return (
              <option key={type.value} value={type.value}>
                {type.name}
              </option>
            );
          })}
        </select>

        <button type="submit">
          Generate &nbsp;
          {loading ? (
            <span
              style={{
                display: "flex",
                alignItems: "center",
                marginLeft: "6px"
              }}
            >
              <ClipLoader color="#13aeb1" loading={loading} size={20} />
            </span>
          ) : (
            <span aria-label="robot" role="img">
              {types.filter((type) => type.value === +currType)[0]?.emoji}
            </span>
          )}
        </button>
      </form>
      {isLoaded && !error ? (
        <>
          <div className="background"></div>
          <img src={imageURL} alt={robohash} loading="lazy" />
        </>
      ) : isLoaded && error ? (
        <div className="error"> Something Went Wrong! </div>
      ) : (
        <div></div>
      )}
      <div className="void"></div>
      <footer>
        Robots lovingly{" "}
        <span aria-label="heart" role="img">
          ðŸ–¤
        </span>{" "}
        delivered by{" "}
        <a href="https://robohash.org" target="_blank" rel="noreferrer">
          Robohash.org
        </a>
        .
        <br /> Created by{" "}
        <a
          href="https://github.com/skotancilar"
          target="_blank"
          rel="noreferrer"
        >
          kotancilar
        </a>{" "}
        .
      </footer>
    </div>
  );
}
